import './App.css';
import Homepage from './pages/Homepage';
import Subscriptions from './pages/Subscriptions';
import React from "react";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";

function App() {
  return (
    <Router>
    <div className="App">
     <Switch>
          <Route path="/subscriptions">
            <Subscriptions />
          </Route>
          <Route path="/">
            <Homepage />
          </Route>
        </Switch>
    </div>
    </Router>
  );
}

export default App;
