import "./content.css";

function Content(props) {
    return <div className='content'>
        <div className='div-content'>
            <img src={props.icon1}></img>
            <h4>{props.title1}</h4>
            <p>{props.description}</p>
        </div>

        <div className='div-content'>

            <img src={props.icon2}></img>
            <h4>{props.title2}</h4>
            <p>{props.description}</p>
        </div>

        <div className='div-content'>

            <img src={props.icon3}></img>
            <h4>{props.title3}</h4>
            <p>{props.description}</p>
        </div>
        
    </div>
}

export default Content;