import './header.css';
import {Link} from "react-router-dom";

function Header() {
    return <div className='header'>
        <p className='logo'>FIT STUDIO</p>
        <nav className='nav-bar'>
            <li>
              <Link className='links' to="/"><span>Home</span></Link>
            </li>
            <li>
              <Link className='links' to="/subscriptions"><span>Subscriptions list</span></Link>
            </li>
        </nav>
    </div>
}

export default Header;